import Vue from 'vue'
import Router from 'vue-router'
import Home from './pages/Home.vue'
import Skills from './pages/Skills.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/skills',
      name: 'skills',
      component: Skills
    }
  ]
})
